import React, {useEffect, useState} from "react";
import style from './Nav.module.css'
import main from './../common/images/main.png'
import {HashLink as Link} from "react-router-hash-link";


export const Nav = () => {
    const [open, setOpen] = useState(false)

    const pages = [
        {className: main, id: 'main'},
        {className: main, id: 'aboutMe'},
        {className: main, id: 'skills'},
        {className: main, id: 'projects'},
        {className: main, id: 'contact'},
    ]


    useEffect( () => {
        document.addEventListener('mousedown',()=> {
            setOpen(!open)
        })
    })


    return (
        <div className={style.navbar}>
            <button className={style.button} onClick={() => setOpen(!open)}>
                <span></span>
                <span></span>
                <span></span>
            </button>

            <div className={` ${open ? style.navBlockOpen : ''} ${style.navBlock}`}>
                <button onClick={state => setOpen(!state)}>X</button>
                <ul className={style.navPages}>
                    {pages.map(el =>
                        <li>
                            <Link smooth to={`#${el.id}`}>
                                <img src={el.className}/>
                            </Link>
                        </li>)}
                </ul>
            </div>
        </div>
    )


}